.testimonial{
    background-color: $azul;
    font-size: 2.4rem;
    padding: 2rem;
    color: $blanco;
    border-radius: 2.2rem;
    
    blockquote{
        padding-left: 5rem;
        position: relative;
        

        &::before{
            content: '';
            background-image: url(../img/comilla.svg);
            background-repeat: no-repeat;
            width: 6rem;
            height: 8rem;
            display: block;
            position: absolute;
            left: -3rem;
        }

    }
    p{
        text-align: right;
        color: $blanco;
    }

}