.alerta {
    padding: .5rem;
    text-align: center;
    color: $blanco;
    font-weight: $bold;
    text-transform: uppercase;
    margin: 1rem 0;

    &.error {
        background-color: $rojo;
    }

    &.exito {
        background-color: $verde;
    }
}

table.propiedades {
    margin-top: 4rem;
    width: 100%;
    border-spacing: 0;

    thead {
        background-color: $azul;

        th {
            color: $blanco;
            padding: 2rem;
        }
    }
    tbody{
        text-align: center;
        .form-box{
            display: flex;
            justify-content: center;
            align-items: center;
            .input-box{
                width: 50%;
            }
        }
        .input-box{
            width: 50%;
        }
        .box-input{
            display: flex;
            justify-content: center;
            align-items: center;
        }
    }

    .imagen-tabla {
        width: 10rem;
    }
}

.imagen-small {
    width: 20rem;
}
